import { styleMargin, JigsawComponentStyle } from '@lowinc/jigsawpuzzle-lib';
import * as React from 'react';
import { uiTarget } from '../../env';
import { createCss } from '../../utils/css';
import './index.css';

interface Props {
  img?: string;
  name?: string;
  desc?: string;
  style: JigsawComponentStyle;
}

const css = createCss('dd-ui-block');

const Block: React.FC<Props> = props => {
  const clientWidth = uiTarget === 'pc' ? 375 : document.body.clientWidth;
  const contentWidth = clientWidth - (props.style?.padding || 0) * 2;
  const itemWith = (contentWidth - (props.style.margin || 0) * 2) / 2;

  return (
    <div
      className={css()}
      style={{
        ...styleMargin(props.style),
        width: `${itemWith}px`,
      }}
    >
      <img className={css('poster')} src={props.img} alt="" />
      <div className={css('content')}>
        <div className={css('name')}>{props.name}</div>
        <div className={css('desc')}>{props.desc}</div>
      </div>
    </div>
  );
};

export default Block;
