import { JigsawComponentStyle, stylePadding } from '@lowinc/jigsawpuzzle-lib';
import * as React from 'react';
import { Carousel } from 'zarm';
import { uiTarget } from '../../env';
import { createCss } from '../../utils/css';
import './index.css';
interface Props {
  onChange: (index: number | undefined) => any;
  style?: JigsawComponentStyle;
}

const css = createCss('dd-ui-layout-swiper');

const LayoutSwiper: React.FC<Props> = props => {
  const clientWidth = uiTarget === 'pc' ? 375 : document.body.clientWidth;
  const paddingWith = clientWidth - (props.style?.padding || 0) * 2;

  return (
    <div style={stylePadding(props.style)}>
      <Carousel
        style={{
          width: paddingWith,
        }}
        prefixCls={css('wrapper')}
        onChange={props.onChange}
        direction="left"
      >
        {props.children as any}
      </Carousel>
    </div>
  );
};

export default LayoutSwiper;
