import {
  JigsawComponents,
  JigsawComponentStyle,
  stylePadding,
} from '@lowinc/jigsawpuzzle-lib';
import * as React from 'react';
import LayoutBlock from '../Layout-Block';
import LayoutCube from '../Layout-Cube';
import LayoutRoll from '../Layout-Roll';
import LayoutSwiper from '../Layout-Swiper';

interface Props {
  componentType: JigsawComponents;
  style?: JigsawComponentStyle;
}

const AppointLayout: React.FC<Props> = props => {
  if (props.componentType === JigsawComponents.Swiper) {
    return (
      <LayoutSwiper style={props.style} onChange={console.log}>
        {props.children}
      </LayoutSwiper>
    );
  }

  if (props.componentType === JigsawComponents.Block) {
    return <LayoutBlock style={props.style}>{props.children}</LayoutBlock>;
  }

  if (props.componentType === JigsawComponents.Roll) {
    return <LayoutRoll style={props.style}>{props.children}</LayoutRoll>;
  }

  if (
    props.componentType === JigsawComponents.Cube ||
    props.componentType === JigsawComponents.CubeRow4
  ) {
    return (
      <LayoutCube componentType={props.componentType} style={props.style}>
        {props.children}
      </LayoutCube>
    );
  }

  return <div style={stylePadding(props.style)}>{props.children}</div>;
};

export default AppointLayout;
