import { JigsawComponentStyle, styleMargin } from '@lowinc/jigsawpuzzle-lib';
import * as React from 'react';
import { createCss } from '../../utils/css';
import './index.css';

interface Props {
  name?: string;
  desc?: string;
  style: JigsawComponentStyle;
}

const css = createCss('dd-ui-title');

const Title: React.FC<Props> = props => {
  console.log(props.style);
  return (
    <div className={css()} style={styleMargin(props.style)}>
      <div className={css('name')}>{props.name}</div>
      <div className={css('desc')}>{props.desc}</div>
    </div>
  );
};

export default Title;
