import { JigsawComponentStyle, stylePadding } from '@lowinc/jigsawpuzzle-lib';
import * as React from 'react';
import { uiTarget } from '../../env';
import { createCss } from '../../utils/css';
import './index.css';

interface Props {
  style?: JigsawComponentStyle;
}

const css = createCss('dd-ui-layout-roll');

const LayoutRoll: React.FC<Props> = props => {
  const clientWidth = uiTarget === 'pc' ? 375 : document.body.clientWidth;
  const paddingWith = clientWidth - (props.style?.padding || 0) * 2;

  return (
    <div className={css('content')} style={stylePadding(props.style)}>
      <div className={css('box')} style={{ width: `${paddingWith}px` }}>
        <div className={css('children')}>{props.children}</div>
      </div>
    </div>
  );
};

export default LayoutRoll;
