import {
  JigsawComponents,
  JigsawComponentStyle,
  stylePadding,
} from '@lowinc/jigsawpuzzle-lib';
import React, { useMemo } from 'react';
import { uiTarget } from '../../env';
import { createCss } from '../../utils/css';
import './index.css';

interface Props {
  style?: JigsawComponentStyle;
  componentType: JigsawComponents.Cube | JigsawComponents.CubeRow4;
}

const css = createCss('dd-ui-layout-cube');

const LayoutCube: React.FC<Props> = props => {
  const style = useMemo(() => {
    const mapSub: Record<Props['componentType'], number> = {
      [JigsawComponents.Cube]: 3,
      [JigsawComponents.CubeRow4]: 4,
    };
    const row = mapSub[props.componentType] || 3;
    // admin 设置了365宽度
    const clientWidth = uiTarget === 'pc' ? 375 : document.body.clientWidth;
    const paddingWith = clientWidth - (props.style?.padding || 0) * 2;
    const unit = Math.floor(paddingWith / row);
    return {
      width: `${unit}px`,
      height: `${unit}px`,
    };
  }, []);

  return (
    <div className={css('content')} style={stylePadding(props.style)}>
      <div className={css('children')}>
        {[...((props.children || []) as any)].map((Element, index) => (
          <div key={index} className={css('item')} style={style}>
            {Element}
          </div>
        ))}
      </div>
    </div>
  );
};

export default LayoutCube;
